@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap");
:root {
  --black: hsl(0, 0%, 0%);
  --neon-purple: hsl(270, 100%, 48%);
  --grayish-blue: #313a48;
  --dark-grayish-blue: #202733;
  --white: white;
  --body-font: "Manrope", sans-serif;
}

* {
  margin: 0;
  padding: 0;
}

body {
  box-sizing: border-box;
  align-items: center;
  background-image: url("../images/calmSky.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--dark-grayish-blue);
  font-family: var(--body-font);
}

.main-container {
  position: relative;
  max-width: 33.75rem;
  width: 90%;
  min-width: 10rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.28rem;
  margin: 0 auto;
  margin-top: 5rem;
  margin-bottom: 5rem;
  /* background-color: var(--grayish-blue); */
  border-radius: 10px;
  padding: 3.2rem 1.5rem;
  text-align: center;
  box-sizing: border-box;

  backdrop-filter: blur(8px) saturate(197%);
  -webkit-backdrop-filter: blur(8px) saturate(197%);
  background-color: rgba(255, 255, 255, 0.3);
  /* border-radius: 12px; */
  border: 1px solid rgba(209, 213, 219, 0.3);
}

.advice-number {
  color: var(--neon-purple);
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 14px;
  font-weight: 800;
}

.quotes {
  font-size: 1.75rem;
  color: var(--black);
  font-weight: 800;
  padding: 0 1rem;
}
.line {
  width: 90%;
  height: 2rem;
  display: flex;
  flex-direction: row;
  padding-top: 0.6rem;
  align-items: center;
  margin: 0 auto;
}
.line::before,
.line::after {
  content: " ";
  background-color: var(--white);
  opacity: 0.2;
  width: 70%;
  height: 1px;
}
.line-image {
  background-image: url("../images/pattern-divider-mobile.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 10rem;
  height: 2rem;
}


.dice-img {
  position: absolute;
  cursor: pointer;
  background-color: var(--neon-purple);
  background-image: url("../images/icon-dice.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  transform: translateY(30%) translateX(-50%);
  left: 50%;
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
  transition: all ease-in-out 0.3s;

}

@media (hover: hover) {
  .dice-img:hover {
    box-shadow: -1px 1px 88px 12px rgba(54, 160, 187, 1);
    -webkit-box-shadow: -1px 1px 88px 12px rgba(54, 160, 187, 1);
    -moz-box-shadow: -1px 1px 88px 12px rgba(54, 160, 187, 1);
    transition: all ease-in-out 0.3s;
  }
}

@media screen and (min-width: 900px) {
  .main-container {
    margin-top: 13.75rem;
  }
}